// import logo from './logo.svg';
import "./App.css";
import { HashRouter as Router, Switch, Route, Link } from "react-router-dom";
import Signup from "./pages/Signup";
import Login from "./pages/Login";
import React from "react";
import { Auth, Hub } from "aws-amplify";
import { useSelector } from "react-redux";
import { clearAuth, clearToast, setToken, setUser, setMode } from "./store/actions";
import { ToastContainer, toast } from "react-toastify";
import "react-toastify/dist/ReactToastify.css";
import { useDispatch } from "react-redux";
import FlexDiv from "./design-system/FlexDiv";
import Button from './design-system/Button';
import Container from './design-system/Container';
import axios from "axios"
import AppContainer from "./components/AppContainer"
import {BASE_URL} from "./constants"

function App() {
  const notificationToast = useSelector((state) => state.notification.toast);
  const token = useSelector((state) => state.auth.token);
  const loggedIn = useSelector((state) => state.auth.loggedIn)
  const mode = useSelector((state) => state.theme.mode)
  const [loading, setLoading] = React.useState(true);
  const dispatch = useDispatch();

  React.useEffect(() => {
    console.log(notificationToast);
    if (
      notificationToast &&
      typeof notificationToast.message === "string" &&
      typeof notificationToast.type === "string"
    ) {
      toast(notificationToast.message, {
        type: notificationToast.type,
      });
      clearToast();
    }
  }, [notificationToast]);
  const getUserOnInit = async () => {
    // get AWS authentication
    // setLoading(true);

    const u = await Auth.currentAuthenticatedUser();
    // get realcapital api authorized user
    // const token = u.signInUserSession.idToken.jwtToken;
    return u;
  };
  React.useEffect(() => {
    // setLoading(true);
    Hub.listen("auth", (res) => {
      if (res.payload.event === "signIn") {
        const u = res.payload.data;

        dispatch(setUser(u));
        dispatch(setToken(u.signInUserSession.idToken.jwtToken));
        toast("Welcome to Leagues", {
          type: "dark",
        });
      }
      if (res.payload.event === "signOut") {
        dispatch(clearAuth());
        toast("User succesfully logged out", {
          type: "dark",
        });
        dispatch(clearToast());
        // dispatch(clearNotification)
      }
    });
    getUserOnInit().then((u) => {
      if (u) {
        //console.log(u);
      
        dispatch(setUser(u));
        dispatch(setToken(u.signInUserSession.idToken.jwtToken));

      }
      //console.log("2nd");
    });
    setLoading(false);
  }, []);

  const validateUserAuthToken = () => {

    axios.get(`${BASE_URL}/api/testuser`, { headers: { idtoken: token, liftedapp: "leagues"} }).then(
      (res) => {
        if (res.status === 200) {
          //console.log(res.data.projects);
          // setListings(res.data.projects);
          console.log(res)
        }
      }
    );
  }


  const handleLogout = async () => {
    // history.push("/login");
    Auth.signOut().then((res) => {
      console.log("signed out");
    });
  };

  const changeTheme = () => {
    console.log(mode);
    
    if(mode ==="dark"){
      dispatch(setMode(
        "light"
      ))
    }
    if(mode ==="light"){
      dispatch(setMode("dark"))
    }
    
  }

  return (
    <AppContainer>
      <Router basename="/">
        <ToastContainer position="bottom-right" />
        <Switch>
          <Route path="/" exact>
            <Container>
            <FlexDiv justify="space-between" align="center">
              
            <h3>Leagues api.</h3>
            {!loggedIn ? (

            <Link to="/login">Login</Link>
            ): (
              <FlexDiv justify="flex-end">
              <Button onClick={()=>{changeTheme()}} label="Change Theme"/>
            <Button
              onClick={() => {
                handleLogout();
              }}
              label="Logout"
            />
</FlexDiv>
            )}
            </FlexDiv>
            <FlexDiv vert>

            <p>Validate user auth token</p>
            <Button onClick={validateUserAuthToken} label="Validate"/>
            </FlexDiv>
            </Container>
          </Route>

          <Route path="/login">
            <Login />
          </Route>
        </Switch>
      </Router>
    </AppContainer>
  );
}

export default App;
